<script>
  export let date;
  export let time;
  export let title;
  export let check = true;
  export let minDate;

  let isFocused = false;
</script>

<style type="text/postcss">.input{font-size:1.5rem;text-transform:lowercase;border-bottom-width:4px;color:#edf2f4;background-color:transparent;margin-bottom:1rem;width:auto}.input-ok{border-color:#2ec4b6}.input-error{border-color:#ef233c}.background{background-color:#2b2d42;color:#8d99ae}.background-selected{color:#ff9f1c}</style>

<div class="background p-4 {isFocused ? 'background-selected' : ''}">
  <h1>
    {title}
    <body>
      <input class="input {check ? 'input-ok' : 'input-error'}" type="date" bind:value={date} on:focus={() => isFocused = true} on:blur={() => isFocused = false} min={minDate}/>
      <input class="input {check ? 'input-ok' : 'input-error'}" type="time" bind:value={time} on:focus={() => isFocused = true} on:blur={() => isFocused = false} />
    </body>
  </h1>
  <slot></slot>
</div>